.container {
  font-family: "Poppins";
  padding: 1.2rem;
}
.maincontainer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  padding-inline: 2rem;
  height: 80vh;
}
.mainLeftContainer {
  flex: 0.5;
}

.mainRightContainer {
  flex: 0.5;
}

.mainLeftContainer > p:nth-child(1) {
  background: -webkit-linear-gradient(45deg , #000000 #9d9d9d);
  background-clip: text;
  -webkit-text-fill-color: rgb(225, 66, 98);
  font-size: 2.5rem;
}

.mainLeftContainer > p:nth-child(2) {
  margin-top: 0.5rem;
  font-size: 1.5rem;
  color: rgb(97, 95, 95);
}

.joinButton {
  width: fit-content;
  margin-top: 0.8rem;
  padding:0.8rem;
  background-color: blueviolet;
  color: white;
  padding-inline: 2rem;
  cursor: pointer;
  border-radius: 10px;
}

.imageBox {
  height: 200px;
  width: 200px;
}
